@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Montserrat', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

body {
	background-color: #2e2e2e;
	color: #e5e5e5;
}

.App {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.Home {
	height: 100vh;
	width: 100vw;
}

.Home .head,
.Home section,
.Home footer {
	width: 100%;
	height: 33.3%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-end;
}

.Home .Logo img {
	width: 100px;
	height: 100px;
}

.Home section {
	justify-content: center;
}

.Home .title {
	font-size: 35px;
	margin-bottom: 20px;
}

.Home .subtitle {
	font-size: 14px;
}

.Home .SearchButton img {
	width: 30px;
	height: 30px;
	margin-bottom: 30px;
}

/*media queries*/
@media (orientation: landscape) and (max-height: 400px) {
	.Home .Logo img {
		width: 80px;
		height: 80px;
	}
	.Home .title {
		font-size: 30px;
	}
	.Home .subtitle {
		font-size: 14px;
	}
	.Home .SearchButton img {
		width: 30px;
		height: 30px;
		margin-bottom: 30px;
	}
}

@media (min-width: 700px) {
	.Home .Logo img {
		width: 150px;
		height: 150px;
	}
	.Home .title {
		font-size: 45px;
	}
	.Home .subtitle {
		font-size: 16px;
	}
	.Home .SearchButton img {
		width: 40px;
		height: 40px;
		margin-bottom: 40px;
	}
}

@media (min-width: 900px) {
	.Home .title {
		font-size: 70px;
	}
	.Home .subtitle {
		font-size: 22px;
	}
	.Home .SearchButton img {
		width: 60px;
		height: 60px;
		margin-bottom: 60px;
	}
}

@media (min-width: 1400px) {
	.Home .Logo img {
		width: 250px;
		height: 250px;
	}
	.Home .title {
		font-size: 90px;
	}
	.Home .subtitle {
		font-size: 28px;
	}
	.Home .SearchButton img {
		width: 70px;
		height: 70px;
		margin-bottom: 70px;
	}
}

@media (min-width: 2000px) {
	.Home .Logo img {
		width: 400px;
		height: 400px;
	}
	.Home .title {
		font-size: 130px;
	}
	.Home .subtitle {
		font-size: 45px;
	}
	.Home .SearchButton img {
		width: 100px;
		height: 100px;
		margin-bottom: 100px;
	}
}

