.InputArea {
	margin-top: 10px;
	display: flex;
	justify-content: center;
}

.InputArea .input-area {
	width: 200px;
	height: 40px;
	border: 1px solid #42cf47;
	border-radius: 10px 0 0 10px;
	font-size: 15px;
	text-align: center;
}

.InputArea .input-area::-webkit-input-placeholder {
	text-align: center;
}

.InputArea .input-area:-ms-input-placeholder {
	text-align: center;
}

.InputArea .input-area::-ms-input-placeholder {
	text-align: center;
}

.InputArea .input-area::placeholder {
	text-align: center;
}

.InputArea .submit-button {
	width: 150px;
	border: none;
	background-color: #42cf47;
	border-radius: 0 10px 10px 0;
	font-size: 15px;
}

/*media queries*/
@media (orientation: landscape) and (max-height: 400px) {
	.InputArea .input-area {
		width: 150px;
		height: 30px;
		font-size: 14px;
	}

	.InputArea .submit-button {
		width: 140px;
		font-size: 14px;
	}
}

@media (max-width: 350px) {
	.InputArea {
		width: 100vw;
	}

	.InputArea .input-area {
		width: 140px;
		height: 25px;
		font-size: 14px;
	}

	.InputArea .submit-button {
		width: 140px;
		font-size: 14px;
	}
}

@media (min-width: 360px) {
	.InputArea .input-area {
		width: 180px;
		height: 30px;
	}
}

@media (min-width: 700px) {
	.InputArea .input-area {
		width: 250px;
		height: 40px;
		font-size: 18px;
	}

	.InputArea .submit-button {
		width: 180px;
		font-size: 18px;
	}
}

@media (min-width: 900px) {
	.InputArea .input-area {
		width: 300px;
		font-size: 20px;
	}

	.InputArea .submit-button {
		width: 200px;
		font-size: 20px;
	}
}

@media (min-width: 1400px) {
	.InputArea .input-area {
		width: 350px;
		height: 45px;
		font-size: 22px;
	}

	.InputArea .submit-button {
		width: 250px;
		font-size: 22px;
	}
}

@media (min-width: 2000px) {
	.InputArea .input-area {
		width: 700px;
		height: 100px;
		font-size: 36px;
	}

	.InputArea .submit-button {
		width: 450px;
		font-size: 36px;
	}
}

.Card {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 20px auto;
}

.card-unit {
	width: 300px;
	height: 150px;
	background: #525252;
	border: #2e2e2e 1px solid;
	box-shadow: 0px 5px 3px #11200e;
	border-radius: 5px;
	display: flex;
	flex-direction: column;
	margin-bottom: 15px;
}

.card-unit .top {
	height: 40%;
	width: 100%;
	text-align: center;
}

.card-unit .top .repo-name {
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	font-weight: 500;
}

.Card img {
	width: 16px;
	height: 16px;
}

.card-unit .bottom {
	height: 60%;
	width: 100%;
}

.card-unit .info,
.card-unit .labels {
	height: 50%;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	font-weight: 300;
}

.card-unit .info {
	align-items: flex-end;
	color: #42cf47;
	font-weight: 500;
}

.card-unit .info-box {
	width: 33%;
	text-align: center;
}

.card-unit .labels {
	font-size: 12px;
}

/*media queries*/

@media (max-width: 350px) {
	.Card {
		width: 90%;
	}

	.card-unit {
		width: 250px;
		height: 125px;
		margin-bottom: 20px;
	}

	.card-unit .top .repo-name {
		font-size: 14px;
	}

	.Card img {
		width: 12px;
		height: 12px;
	}

	.card-unit .info {
		font-size: 14px;
	}
	.card-unit .labels {
		font-size: 12px;
	}
}

@media (min-width: 700px) {
	.Card {
		width: 90%;
		flex-direction: row;
		flex-wrap: wrap;
		margin-top: 40px;
		justify-content: space-evenly;
	}

	.card-unit {
		width: 300px;
		height: 150px;
		margin-bottom: 50px;
	}

	.card-unit .top .repo-name {
		font-size: 15px;
	}

	.Card img {
		width: 13px;
		height: 13px;
	}

	.card-unit .info {
		font-size: 13px;
	}
	.card-unit .labels {
		font-size: 11px;
	}
}

@media (min-width: 900px) {
	.Card {
		margin-top: 60px;
	}

	.card-unit {
		width: 375px;
		height: 175px;
	}

	.card-unit .top .repo-name {
		font-size: 20px;
	}

	.Card img {
		width: 14px;
		height: 14px;
	}

	.card-unit .info {
		font-size: 16px;
	}
	.card-unit .labels {
		font-size: 12px;
	}
}

@media (min-width: 1400px) {
	.Card {
		margin-top: 80px;
	}

	.card-unit {
		width: 400px;
		height: 200px;
	}

	.card-unit .top .repo-name {
		font-size: 24px;
	}

	.Card img {
		width: 20px;
		height: 20px;
	}

	.card-unit .info {
		font-size: 22px;
	}
	.card-unit .labels {
		font-size: 16px;
	}
}

@media (min-width: 2000px) {
	.Card {
		margin-top: 140px;
	}

	.card-unit {
		width: 700px;
		height: 350px;
	}

	.card-unit .top .repo-name {
		font-size: 48px;
	}

	.Card img {
		width: 34px;
		height: 34px;
	}

	.card-unit .info {
		font-size: 34px;
	}
	.card-unit .labels {
		font-size: 28px;
	}
}

.Label {
	text-align: center;
	margin-top: 20px;
	color: #42cf47;
	font-weight: 600;
}

/*media queries*/

@media (min-width: 700px) {
	.Label {
		margin-top: 40px;
		font-size: 24px;
	}
}

@media (min-width: 900px) {
	.Label {
		font-size: 32px;
	}
}

@media (min-width: 1400px) {
	.Label {
		margin-top: 60px;
		font-size: 40px;
	}
}

@media (min-width: 2000px) {
	.Label {
		margin-top: 80px;
		font-size: 60px;
	}
}

.Pagination {
	display: flex;
	justify-content: center;
	margin-bottom: 20px;
}

.pagination-list {
	display: flex;
	justify-content: center;
	list-style: none;
}

.pages {
	margin: 0 5px;
}

.page-number,
.first-page,
.last-page {
	width: 30px;
	height: 30px;
	border-radius: 50%;
	border: none;
	background-color: #42cf47;
	font-weight: 600;
}

.first-page,
.last-page {
	background-color: #2e2e2e;
	border: 1px solid #42cf47;
	color: #42cf47;
}

.first-page {
	margin-right: 5px;
}

.last-page {
	margin-left: 5px;
}

.page-number:focus {
	background-color: #e5e5e5;
	color: #42cf47;
}

/*media queries*/

@media (min-width: 700px) {
	.pages {
		margin: 0 10px;
	}

	.page-number,
	.first-page,
	.last-page {
		width: 35px;
		height: 35px;
		font-size: 20px;
	}

	.first-page {
		margin-right: 10px;
	}

	.last-page {
		margin-left: 10px;
	}
}

@media (min-width: 900px) {
	.Pagination {
		margin-top: 60px;
	}

	.page-number,
	.first-page,
	.last-page {
		width: 50px;
		height: 50px;
		font-size: 24px;
	}
}

@media (min-width: 1400px) {
	.Pagination {
		margin-top: 80px;
	}

	.pages {
		margin: 0 20px;
	}

	.page-number,
	.first-page,
	.last-page {
		width: 60px;
		height: 60px;
		font-size: 30px;
	}

	.first-page {
		margin-right: 20px;
	}

	.last-page {
		margin-left: 20px;
	}
}

@media (min-width: 2000px) {
	.Pagination {
		margin-top: 100px;
	}

	.pages {
		margin: 0 30px;
	}

	.page-number,
	.first-page,
	.last-page {
		width: 100px;
		height: 100px;
		font-size: 40px;
	}

	.first-page {
		margin-right: 30px;
	}

	.last-page {
		margin-left: 30px;
	}
}

